import { InputTaskInfo } from "@/components/ui/InputTaskInfo/InputTaskInfo";

export function TopInfoTask() {
  return (
    <div className="w-full bg-[#243657]">
      <div className="flex gap-[20px] flex-wrap p-4 w-300">
        <InputTaskInfo type="text" placeholder="Выбранная задача" label="Выбранная задача"/>
        <InputTaskInfo type="date" placeholder="Дата создания задачи" label="Дата создания задачи"/>
        <InputTaskInfo type="date" placeholder="Дата примерного окончания" label="Дата примерного окончания"/>
        <InputTaskInfo type="date" placeholder="Дата окончания задачи" label="Дата окончания задачи"/>
        <InputTaskInfo type="text" placeholder="Автор постановки задачи" label="Автор постановки задачи"/>
        <InputTaskInfo type="text" placeholder="Исполнитель задачи" label="Исполнитель задачи"/>
      </div>
    </div>
  )
}
