import LeftMenuBtn from "../../components/ui/LeftMenuBtn/LeftMenuBtn"

const btnIconArr = [
    {
        imgSrc: './task.svg',
        tag: 'Отслеживание заданий'
    },
    {
        imgSrc: './plus.svg',
        tag: 'Добавить задание'
    },
    {
        imgSrc: './qu.svg',
        tag: 'О программе'
    },
    {
        imgSrc: './logout.svg',
        tag: 'Выход'
    }
]

function LeftMenu() {
    return (
            <div className="flex flex-col gap-[64px] pt-4 items-start justify-start border-r-1 border-r-gray-500 w-60 h-screen bg-[#243657]">
                <img className="w-10 h-auto" src="./logo.png"/>
                <ul className="flex flex-col items-start gap-[20px] w-15">
                    {btnIconArr.map((item) => (
                        <li key={item.tag} className="flex items-center justify-center gap-[12px] pt-2 pb-2 pl-2 border-t-1 border-b-1 border-transparent hover:border-t-1 hover:border-b-1 hover:border-white cursor-pointer">
                            <LeftMenuBtn iconTag={item.tag}>
                                <img className="block w-8 h-8" src={item.imgSrc}/>
                            </LeftMenuBtn>
                        </li>
                    ))}
                </ul>
            </div>
    )
}

export default LeftMenu